h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
}

h3 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.6rem;
}

// Gallery.scss
.gallery-images-block {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.gallery-image {
  cursor: pointer;
  max-width: 150px;
  max-height: 150px;
  transition: transform 0.2s ease;
}

.gallery-image:hover {
  transform: scale(1.1);
}

.gallery-title {
  font-size: 1.5rem;
  margin-bottom: 15px;
}

.link-style {
  display: block;
  width: 100px;
  padding: 10px 20px;
  margin: 20px;
  background-color: #4caf50; /* Green background */
  color: white; /* White text */
  font-size: 16px; /* Font size */
  font-weight: bold; /* Bold text */
  border-radius: 5px; /* Rounded corners */
  text-decoration: none; /* Remove underline */
  transition: background-color 0.3s ease, transform 0.2s ease; /* Smooth transition */

  /* Add some shadow for a 3D effect */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

nav {
  display: flex;
  justify-content: center;
  flex-direction: row;
}
