// Modal.scss
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  position: relative;
  background: white;
  padding: 20px;
  border-radius: 8px;
}

.modal-image {
  max-width: 100%;
  max-height: 80vh;
  object-fit: cover;
  border-radius: 15px;
}

.close-button {
  position: absolute;
  top: -5px;
  right: 10px;
  font-size: 34px;
  cursor: pointer;
  background: none;
  border: none;
}

