@import "./components/mixins.scss";
// @import "header";

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  //   font-family: "Montserrat", sans-serif;
  font-family: "Manrope", sans-serif;
  color: #000000;
}

img {
  max-width: 100%;
}

.container {
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.none {
  display: none !important;
}

/* Header */
.header {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  align-items: start;
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-position: center;
}

.header-title {
  font-family: "Manrope", sans-serif;
  display: block;

  margin: 0;
  //   margin-bottom: 25px;
  font-weight: 400;
  //   font-size: 56px;
  @include adaptiv-font(20, 13);
  //   line-height: 150%;
  //   text-align: center;
  color: black;
  z-index: 2;
  letter-spacing: 0.3rem;
}

.header-subtitle {
  margin: 0;
  font-weight: bold;
  font-size: 16px;
  line-height: 150%;
  text-align: center;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  color: #ffffff;
  z-index: 2;
}

.header-arrow {
  width: 48px;
  height: 48px;
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 2;
}

video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  min-width: 100%;
  min-height: 100%;
  z-index: -1;
  object-position: center;
  object-fit: cover;
}

.header_logo {
  //   width: 300px;
  display: block;
  @include adaptiv-value(width, 150, 60, 0);
  height: auto;
  @include adaptiv-value(margin-right, 40, 10, 0);
  margin-bottom: 5px;
  z-index: 2;
  //   margin-left: 40px;
  //   position: absolute;
  //   top: 0;
  //   left: 0;
}

.header_blocks {
  display: flex;
  flex-direction: row;
  justify-content: start;
}

.block_logo_title {
  display: flex;

  align-items: center;
  //   justify-content: start;
}
