/* Modal Overlay */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7); /* Semi-transparent background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999; /* Make sure it's above other content */
}

/* Modal Content */
.modal-content {
  background-color: #fff; /* White background for the modal */
  padding: 20px;
  border-radius: 10px; /* Rounded corners */
  width: 80%; /* Responsive width */
  max-width: 750px; /* Maximum width for larger screens */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Subtle shadow */
  position: relative; /* Required for close button positioning */
  z-index: 1000; /* Ensure it is above the overlay */
  animation: fadeIn 0.3s ease-in-out; /* Smooth fade-in animation */
}

/* Close Button */
.close-btn {
  background-color: transparent; /* Transparent background */
  border: none; /* No border */
  font-size: 1.5rem; /* Increase icon size */
  cursor: pointer; /* Pointer cursor on hover */
  position: absolute; /* Absolute positioning */
  top: 15px;
  right: 15px;
  color: #555; /* Default color */
  transition: color 0.3s ease; /* Smooth hover transition */
}

.close-btn:hover {
  color: black; /* Red color on hover */

  /* Slider or Image Section */
  .pics {
    margin-top: 20px; /* Space above the slider */
    display: flex;
    justify-content: center; /* Center the slider or images */
    align-items: center;
    overflow: hidden;
    border-radius: 8px; /* Rounded edges for the slider */
  }

  /* Smooth Fade-in Animation for Modal */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: scale(0.9);
    }
    to {
      opacity: 1;
      transform: scale(1);
    }
  }
}
