/* Slider.css */
.slider-container {
  // width: 600px;
  // height: 600px;
  width: 90%;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}

.slide {
  display: flex;
  justify-content: center;
}

.slide img {
  width: 100%;
  border-radius: 10px;
}

.slider-btn {
  position: absolute;
  top: 50%;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  transform: translateY(-50%);
}

.prev-btn {
  left: 10px;
}

.next-btn {
  right: 10px;
}

.dots {
  text-align: center;
  margin-top: 10px;
}

.dot {
  height: 15px;
  width: 15px;
  margin: 0 5px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  cursor: pointer;
}

.dot.active {
  background-color: #717171;
}
