@import "./mixins.scss";

.infoMain {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  p {
    text-align: center;
    @include adaptiv-font(40, 20);
  }
  ul li {
    // text-align: center;

    @include adaptiv-font(25, 14);
    // letter-spacing: 5px;
    @include adaptiv-value(letter-spacing, 5, 2, 0);
  }
}

.infoMain p:first-child {
  font-weight: bold;
  @include adaptiv-font(35, 20);
}

.infoPhoneMail {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  p {
    font-weight: bold;
    // text-decoration: underline;
    @include adaptiv-font(40, 20);
  }

  .infoIcons {
    width: 100%;
    min-height: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    .icon {
      // width: 80px;
      @include adaptiv-value(width, 60, 35, 0);
      // height: 80px;
      @include adaptiv-value(height, 60, 35, 0);
      // margin: 15px;
      @include adaptiv-value(margin, 15, 5, 0);
    }
  }
}

.blockMain {
  width: 100%;
  min-height: auto;
  display: flex;
  justify-content: center;
  align-items: center;

  .infoBlock {
    // width: 500px;
    min-height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    @include adaptiv-font(35, 20);
  }

  .iconSVG {
    display: flex;
    justify-content: center;
    align-items: center;
    // width: 50px;
    // min-height: 50px;
    @include adaptiv-value(width, 50, 20, 0);
    // height: 80px;
    @include adaptiv-value(height, 50, 20, 0);
    @include adaptiv-value(margin, 10, 2, 0);
  }
}

